.player {
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

input {
  width: 100%;
  -webkit-appearance: none;
  background: transparent;
  cursor: pointer;
}

.time-control {
  width: 50%;
  display: flex;
  input {
    width: 100%;
    -webkit-appearance: none;
    background: transparent;
    cursor: pointer;
  }

  p {
    padding: 1rem;
  }
}

.play-control {
  width: 30%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;

  svg {
    cursor: pointer;
  }
}

input[type=range]:focus {
  outline: none;
}

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 1px;
  height: 16px;
}

input[type=range].volume-range::-webkit-slider-thumb {
  width: 5px;
  height: 6px;
}

input[type=range].volume-range {
  position: absolute;
}

.mn-h {
  margin: 15px 0px;
  height: 6px !important;
}

input[type=range]::-moz-range-thumb {
  -webkit-appearance: none;
  background: transparent;
  border: none;
}


.track {
  margin: 1rem 0rem;
  background: lightblue;
  width: 100%;
  height: 1rem;
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
}

.animate-track {
  width: 100%;
  height: 100%;
  background: rgb(164, 164, 164);
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(0%);
  pointer-events: none;
}

.volume-control {
  width: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.volume-icons {
  width: 15px;
  height: 15px;
  position: relative;
  margin-right: 10px;
  svg {
    position: absolute;
    top: 0;
    left: 0;
  }
}


@media screen and (max-width: 768px) {
  .time-control {
    width: 90%;
  }
  .play-control {
    width: 60%;
  }

  .volume-control {
    width: 40%;
    margin-right: 15px;
  }
}